*{margin:0;padding:0;font-family:"Pretendard",Apple SD Gothic Neo,돋움,dotum,sans-serif;font-weight:400;font-size:inherit;word-break:break-all}html,body{width:100%;height:100%;color:#111}body{background-color:#fff}body{cursor:url("../images/mouse.png") 20 20,auto;padding:env(safe-area-inset-top, 20px) env(safe-area-inset-right, 20px) env(safe-area-inset-bottom, 20px) env(safe-area-inset-left, 20px)}h1,h2,h3,h4,p{margin:0}ul,ol,li,dl,dt,dd{list-style:none;margin:0;padding:0}button{padding:0;border:0;background-color:rgba(0,0,0,0);cursor:pointer}a{color:inherit;text-decoration:none}a:hover,button:hover,label:hover{cursor:url("../images/mouse_link.png") 20 20,auto}#wrapper{min-height:100vh;background-color:#fff}#container{position:relative;height:100%;min-height:100vh;padding:0;box-sizing:border-box;background-color:#fff}#header{position:fixed;width:100%;z-index:1}#header .header-logo{mix-blend-mode:soft-light}#header .header-logo img{display:block;width:100%;height:80px}@media(max-width: 500px){.header-logo img{height:50px}}